'use strict';

/**
* WebAssembly routine to add a scalar constant to each element in a double-precision floating-point strided array.
*
* @module @stdlib/blas-ext-base-wasm-dapx
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dapx from '@stdlib/blas-ext-base-wasm-dapx';
*
* // Define a strided array:
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* // Perform operation:
* dapx.main( x.length, 5.0, x, 1 );
* // x => <Float64Array>[ 3.0, 6.0, 8.0, 0.0, 9.0, 5.0, 4.0, 2.0 ]
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dapx from '@stdlib/blas-ext-base-wasm-dapx';
*
* // Define a strided array:
* var x = new Float64Array( [ -2.0, 1.0, 3.0, -5.0, 4.0, 0.0, -1.0, -3.0 ] );
*
* // Perform operation:
* dapx.ndarray( x.length, 5.0, x, 1, 0 );
* // x => <Float64Array>[ 3.0, 6.0, 8.0, 0.0, 9.0, 5.0, 4.0, 2.0 ]
*
* @example
* import Memory from '@stdlib/wasm-memory';
* import oneTo from '@stdlib/array-one-to';
* import zeros from '@stdlib/array-zeros';
* import dapx from '@stdlib/blas-ext-base-wasm-dapx';
*
* // Create a new memory instance with an initial size of 10 pages (640KiB) and a maximum size of 100 pages (6.4MiB):
* var mem = new Memory({
*     'initial': 10,
*     'maximum': 100
* });
*
* // Create a BLAS routine:
* var mod = new dapx.Module( mem );
* // returns <Module>
*
* // Initialize the routine:
* mod.initializeSync();
*
* // Define a vector data type:
* var dtype = 'float64';
*
* // Specify a vector length:
* var N = 3;
*
* // Define a pointer (i.e., byte offset) for storing the input vector:
* var xptr = 0;
*
* // Write vector values to module memory:
* mod.write( xptr, oneTo( N, dtype ) );
*
* // Perform computation:
* mod.main( N, 5.0, xptr, 1 );
*
* var view = zeros( N, dtype );
* mod.read( xptr, view );
* // view => <Float64Array>[ 6.0, 7.0, 8.0 ]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import Module from './module.js';


// MAIN //

setReadOnly( main, 'Module', Module );


// EXPORTS //

export default main;

// exports: { "Module": "main.Module" }
